.section-title {
    color: #fff;
    text-align: center;
    font-size: 32px;
    font-weight: 500;
    line-height: 32px;
    letter-spacing: -1.5px;

    @include on-tablet {
        font-size: 48px;
    }

    @include on-desktop {
        font-size: 64px;
    }

    &--align--left {
        text-align: left;
    }

}
