/* Start nav */
.nav {

    &__list {
        margin: 0;
        padding: 0;
        list-style: none;

        display: flex;
        flex-direction: column;
        gap: 32px;
    }

    &__link {
        color: #1d1d1d;
        text-decoration: none;

        font-size: 22px;
        line-height: 22px;
        letter-spacing: 2px;

        position: relative;

        &::after {
          content: "";
          position: absolute;
          bottom: -8px;
          display: block;
          width: 100%;
          height: 1px;
          background-color: $main-text-color;

          transition: transform 0.3s;
          transform: scale(0);
        }

        &:hover::after {
          transform: scale(1);
        }
    }
}


/* End nav */
